import React,{ Component } from 'react';
import styled from 'styled-components';
// import BestVineArr from '../../Assets/Vine/Vine';
import LinkBtn from '../../Components/LinkBtn/LinkBtn';
import Modal from 'react-modal';
import './VinePage.css'
import { connect } from 'react-redux';
import * as actionTypes from '../../Actions/consts';
import { initialState } from '../../Reducers/VineCollection';

const Ul = styled.ul`
    list-style:none;
    width:500px;
    margin-left:-7%;
    display:flex;
    justify-content: space-between;
    flex-wrap:wrap;`
const Li = styled.li`
    float:left;
    margin: 20px 50px 0 0;`
const CaptionG = styled.h1`
    width: 245px;
    height: 34px;
    font-size: 28px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 1.2px;
    color: #a1a1a1;`
const Lip = styled.p`
    width: 35px;
    height: 26px;
    font-size: 14px;
    font-weight: normal;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.86;
    letter-spacing: 0.2px;
    color: #989898;`
const CounterContainer = styled.div` 
    width: 90px; 
    height: 40px; 
    border-radius: 4px; 
    background-color: #efefef; 
    display:flex; 
    justify-content:space-between; 
    align-items:center;` 
const Button = styled.button` 
    width: 272px; 
    height: 50px; 
    border-radius: 4px; 
    background-image: linear-gradient(to right, #da605e, #c53237); 
    text-align:center; 
    vertical-align:middle; 
    border:0; 
    margin-top:20px;`
const ButtonText = styled.span`
    width: 202px;
    height: 18px;
    font-size: 14px;
    font-weight: bold;
    font-style: normal;
    font-stretch: normal;
    line-height: normal;
    letter-spacing: 0.3px;
    text-align: center;
    color: #ffffff;`

class VinePage extends Component {
    state = {
        sum: 1,
        open: false
    }

    openModal = () => {
        this.setState({open: true});
    }
    closeModal = () => {
        this.setState({open: false});
    }
    increaseHandler = () => {
        this.setState({sum: this.state.sum + 1});
    }
    decreaseHandler = () => {
        this.setState({sum: this.state.sum - 1});
    }
    componentWillMount() {
        Modal.setAppElement('body');
    }

    render() {
    const width = {width: '100%'}
    const height = {height: '700px'}
    const margin = {marginTop: '2%'}
    const marginB = {transform: 'translateY(-300px)'}
    const size = {fontSize: '20px'}
    const index = localStorage.getItem('items');
        return (
            <div className='container-vine'>
            <div className='containerIn' style={height}>
                <LinkBtn to='/Vine' >
                <img alt='arrow' src={require('../../Assets/Arrow/miniArrow.svg')} />    <span className='caption-vine'>Вино</span>
                </LinkBtn>
                <div className='img-container-vine'>
                <img width='151px' height='559px' src={initialState.vine[index].src} alt='vine' />
                </div>
                <div className='vine-right-side'>
                    <br />
                    <p className='vine-caption-item'>
                        {initialState.vine[index].fullName}
                    </p>
                    <p className='vine-text'>{initialState.vine[index].des}</p>
                    <strong>Узнать больше</strong> 
                    <br /> 
                    <div className='vine-count'> 
                        <strong style={size}>{initialState.vine[index].cost * this.state.sum} грн</strong> 
                        <span> {initialState.vine[index].value}</span> 
                        <CounterContainer> 
                            <span onClick={this.state.sum === 1 ? null : this.decreaseHandler}>-</span> 
                            <span>{this.state.sum}</span> 
                            <span onClick={this.increaseHandler}>+</span> 
                        </CounterContainer> 
                    </div> 
                    <br /> 
                    <Button onClick={this.openModal}>
                        <ButtonText>
                            Добавить в корзину
                        </ButtonText>
                    </Button>
                    <Ul>
                        <Li>
                            <Lip>Тип</Lip>
                            <p>{initialState.vine[index].type}</p>
                        </Li>
                        <Li>
                            <Lip>Цвет</Lip>
                            <p>{initialState.vine[index].color}</p>
                        </Li>
                        <Li>
                            <Lip>Сладость</Lip>
                            <p>{initialState.vine[index].sweet}</p>
                        </Li>
                        <Li>
                            <Lip>Страна</Lip>
                            <p>{initialState.vine[index].country}</p>
                        </Li>
                        <Li>
                            <Lip>Сорт</Lip>
                            <p>{initialState.vine[index].sort}</p>
                        </Li>
                        <Li>
                            <Lip>Регион</Lip>
                            <p>{initialState.vine[index].region}</p>
                        </Li>
                        <Li>
                            <Lip>Вкус</Lip>
                            <p>{initialState.vine[index].taste}</p>
                        </Li>
                    </Ul>
                </div>
            </div>
            <div className='containerIn' style={margin}>
                <CaptionG>Описание</CaptionG>
                <br />
                <p className='vine-text' style={width}>
                    {initialState.vine[index].fullDes}
                </p>
            </div>
            <div className='containerIn' style={margin}>
                <CaptionG>Производитель</CaptionG>
                <br />
                <img src={initialState.vine[index].prodImg} width='581px' height='386px' alt='ProdImg' />
                <div className='vine-right-side' style={marginB}>
                    <p className='vine-caption-item'>{initialState.vine[index].prod}</p>
                    <p className='vine-text'>
                        {initialState.vine[index].prodDes}
                    </p>
                </div>
            </div>
            <Modal
                isOpen={this.state.open}
                onAfterOpen={this.afterOpenModal}
                onRequestClose={this.closeModal}
                contentLabel="Example Modal"
                className='modal'
                >
                <div className='inmodal'>
                    <img alt='arrow' src={require('../../Assets/Arrow/miniArrow.svg')} />    <strong>Корзина</strong>
                    <hr />
                    <img width='38px' height='139px' src={initialState.vine[index].src} alt='vine' className='modal-img' />
                    <p className='vine-caption-item close-item' onClick={this.closeModal}>X</p>
                    <div className='modal-counter'>
                        <p className='Shanon'>
                            {initialState.vine[index].fullName}
                        </p>
                        <div className='vine-counter'>  
                            <CounterContainer> 
                                <span onClick={this.state.sum === 1 ? null : this.decreaseHandler}>-</span> 
                                <span>{this.state.sum}</span> 
                                <span onClick={this.increaseHandler}>+</span> 
                            </CounterContainer>
                            <span> {initialState.vine[index].value}</span>
                            <strong style={size}>{initialState.vine[index].cost * this.state.sum} грн</strong> 
                        </div>
                    </div>
                    <div className='bottom-side'>
                    <hr />
                        <span className='caption-vine'>Итого</span>
                        <strong style={size} className='marginR'>{initialState.vine[index].cost * this.state.sum} грн 
                        </strong>
                        <LinkBtn to='/Order'>
                            <Button className='modal-button'><ButtonText>Оформить заказ</ButtonText></Button>
                        </LinkBtn>
                    </div>
                </div>
            </Modal>
        </div>
        )
    }
}


const mapDispatchToProps = dispatch => {
    return {
        receiveItem: () => dispatch({type: actionTypes.RECEIVE_ITEM })
    }
}

export default connect(null, mapDispatchToProps)(VinePage);


