const VineCollection = (state = initialState, action) => {
    switch( action.type ) {
        case actionTypes.RECEIVE_ITEM:
        return {
            ...state,
            basket:  action.index
        }
        case actionTypes.DEFAULT_ARR:
        return {
            ...state,
            vineAfter: state.vineNow.concat(state.vine)
        }
        case actionTypes.FILTR_CHOSE:
        return {
            ...state,
            vineAfter: state.vineAfter.filter(el => {for (var key in el) {
                if(el[key] === action.value) {
                    return true;
                }
            }})
        }
        case actionTypes.DEFAULT_RENDER:
        return {
            ...state,
            vineAfter: state.vineNow.concat(state.vine)
        }
        if (typeof action.value === 'undefined' ) {
            return initialState
        }
        default:
            return {
                ...state
            };
    }

}

export default VineCollection;


