import React, { Component } from 'react';
import styled from 'styled-components';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { connect } from 'react-redux';
import * as actionTypes from '../../../Actions/consts';

const Container = styled.div`
    display: inline-flex;
    background: #f9f9f9;
    width: 1000px;
    flex-wrap:none;
`

class Dropdown extends Component {
    state = {
        type: '',
        color: '',
        swetest: '',
        country: '',
        sort: '',
        region: '',
        taste: '',
        value: ''
      };

      typeChange = event => {this.setState({ type: event.target.value })};
      colorChange = event => {this.setState({ color: event.target.value })};
      swetestChange = event => {this.setState({ swetest: event.target.value })};
      countryChange = event => {this.setState({ country: event.target.value })};
      sortChange = event => {this.setState({ sort: event.target.value })};
      regionClick = event => {this.setState({ region: event.target.value })};
      tasteChange = event => {this.setState({ taste: event.target.value })};
      valueChange = event => {this.setState({ value: event.target.value })};
    render() {
        const pad = {marginRight: '5%'}
        const width = {width: '100px'}
        const type = 'Громкое' 
        const color = 'Белое' 
        const sweetest = 'Полусладкое' 
        const country = 'Италия' 
        const sort = 'Mount Bullet' 
        const region = 'Савойя' 
        const taste = 'Тяжелое' 
        const value = '900мл'
        return(
            <Container>
                <FormControl style={pad}>
                    <InputLabel>Тип</InputLabel>
                    <Select 
                        value={this.state.type}
                        style={width}
                        onClick={this.typeChange}
                        onChange={() => this.props.filtr("Громкое")}>
                        <MenuItem >
                            <em>None</em>
                        </MenuItem>
                        <MenuItem>Громкое</MenuItem>
                    </Select>
                </FormControl>
                <FormControl style={pad}>
                    <InputLabel>Цвет</InputLabel>
                    <Select
                        value={this.state.color}
                        style={width}
                        onClick={this.colorChange}
                        onChange={() => this.props.filtr("Черное")}>
                        <MenuItem >
                            <em>None</em>
                        </MenuItem>
                        <MenuItem value={'Черное'}>Черное</MenuItem>
                    </Select>
                </FormControl>
                <FormControl style={pad}>
                    <InputLabel >Сладкость</InputLabel>
                    <Select 
                        value={this.state.swetest}
                        style={width}
                        onClick={this.swetestChange}
                        onChange={() => this.props.filtr(this.state.sweetest)}>
                        <MenuItem >
                            <em>None</em>
                        </MenuItem>
                        <MenuItem value={'Сладкое'}>Сладкое</MenuItem>
                    </Select>
                </FormControl>
                <FormControl style={pad}>
                    <InputLabel>Страна</InputLabel>
                    <Select 
                        value={this.state.country}
                        style={width}
                        onClick={this.countryChange}
                        onChange={() => this.props.filtr("Испания")}>
                        <MenuItem >
                            <em>None</em>
                        </MenuItem>
                        <MenuItem value={'Испания'}>Испания</MenuItem>
                    </Select>
                </FormControl>
                <FormControl style={pad}>
                    <InputLabel>Сорт</InputLabel>
                    <Select 
                        value={this.state.sort}
                        style={width}
                        onClick={this.sortChange}
                        onChange={() => this.props.filtr(this.state.sort)}>
                        <MenuItem >
                            <em>None</em>
                        </MenuItem>
                        <MenuItem value={'Mount Shield'}>Mount Shield</MenuItem>
                    </Select>
                </FormControl>
                <FormControl style={pad}>
                    <InputLabel>Регион</InputLabel>
                    <Select 
                        value={this.state.region}
                        style={width}
                        onClick={this.regionClick}
                        onChange={() => this.props.filtr(this.state.region)}>
                        <MenuItem >
                            <em>None</em>
                        </MenuItem>
                        <MenuItem value={'НеСавойя'}>НеСавойя</MenuItem>
                    </Select>
                </FormControl>
                <FormControl style={pad}>
                    <InputLabel>Вкус</InputLabel>
                    <Select 
                        value={this.state.taste}
                        style={width}
                        onClick={this.tasteChange}
                        onChange={() => this.props.filtr(this.state.taste)}>
                        <MenuItem >
                            <em>None</em>
                        </MenuItem>
                        <MenuItem value={'Тяжелое'}>Тяжелое</MenuItem>
                    </Select>
                </FormControl>
                <FormControl style={pad}>
                    <InputLabel>Объем</InputLabel>
                    <Select 
                        value={this.state.value}
                        style={width}
                        onClick={this.valueChange}
                        onChange={() => this.props.filtr(this.state.value)}>
                        <MenuItem >
                            <em>None</em>
                        </MenuItem>
                        <MenuItem value={'800 мл'}>800 мл</MenuItem>
                    </Select>
                </FormControl>
                <button onClick={this.props.reRender}>Отмена</button>
            </Container>
        )
    }
}



const mapDispatchToProps = dispatch => {
    return {
        filtr: (item) => dispatch({type: actionTypes.FILTR_CHOSE, value:item}),
        renderArr: () => dispatch({type:actionTypes.DEFAULT_ARR}),
        reRender: () => dispatch({type: actionTypes.DEFAULT_RENDER})
    }
}

export default connect(null, mapDispatchToProps)(Dropdown);


